import React, { Component } from "react";
import socketIOClient from "socket.io-client";
import ReactSpeedometer from "react-d3-speedometer";
import { LineChart } from "react-d3-components";

class App extends Component {
  constructor() {
    super();

    this.state = {
      TRY: 0,
      date: "",
      endpoint: "http://10.1.2.176:5555"
    };
  }

  componentDidMount() {
    const { endpoint } = this.state;
    const socket = socketIOClient(endpoint);
    console.log(`${endpoint} adresine bağlantı yapılıyor...`);

    socket.on("output road", data => {
      this.setState({
        TRY: data.engineData.rates.TRY,
        date: data.engineData.date
      });

      console.log(`Gelen bilgi : ${data.data}`);
    });
  }

  render() {
    const { TRY } = this.state;
    const { date } = this.state;

    return (
      <div style={{ textAlign: "center" }}>
        <h2>1 Dollar</h2>

        <ReactSpeedometer
          maxValue={10}
          minValue={0}
          value={TRY}
          needleColor="gray"
          startColor="orange"
          segments={1}
          endColor="red"
          needleTransition={"easeElastic"}
          ringWidth={20}
          textColor={"black"}
        />
      </div>
    );
  }
}

export default App;
